import { action, observable } from 'mobx';

export const initialCounter = {
  count: 0,
};

/*
  TS tsconfig   decorator    , JS   .
    CRA   react-app-rewired customize-cra  decorator  .
  (config-overrides.js )
  ---
  mobx     .
   decorator    , mobx   make(Auto)Observable  .
*/

class CounterStore {
  @observable count = 0

  constructor(initialData = initialCounter) {
    // makeAutoObservable    
    // @observable, @action decorator .
    this.count = initialData.count;
  }

  @action setCount = (count) => {
    this.count = count;
  }
}

export default CounterStore;
